import React, { Suspense } from "react";
import { Button, CardKelas } from "../../../component";
const Hero = React.lazy(() => import("../../../component/Hero"));
const ChangeKelas = React.lazy(() => import("../../../component/ChangeKelas"));
const About = React.lazy(() => import("../../../component/About"));

const Perkebunan = () => {
  const PerkebunanItem = [
    {
      name: "Budidaya Kelapa Sawit",
      mentor: "Tholib",
      background:
        "./assets/img/changekelas/perkebunan/budidaya-kelapa-sawit.png",
      to: "/budidaya-kelapa-sawit",
      disable: true,
    },
    {
      name: "Budidaya Kopi",
      mentor: "Tholib",
      background: "./assets/img/changekelas/perkebunan/budidaya-kopi.png",
      to: "/budidaya-kopi",
      disable: true,
    },
    {
      name: "Budidaya Teh",
      mentor: "Tholib",
      background: "./assets/img/changekelas/perkebunan/budidaya-teh.png",
      to: "/budidaya-teh",
      disable: true,
    },
  ];
  return (
    <main>
      <Suspense fallback={<div>Loading...</div>}>
        <Hero page="perkebunan">
          <Button
            color="primary"
            bgcolor="bgsecondary"
            onClick={() => window.location.replace("/perkebunan#changekelas")}
          >
            Lihat Daftar Kelas
          </Button>
        </Hero>
        <About page="perkebunan">
          <Button color="primary" styles="outline">
            Pelajari Lebih Lanjut
          </Button>
        </About>
        <ChangeKelas id={"changekelas"}>
          {PerkebunanItem.map((item) => {
            return (
              <CardKelas
                bgImg={item.background}
                disable={item.disable}
                url={item.to}
              >
                {item.name}
              </CardKelas>
            );
          })}
        </ChangeKelas>
      </Suspense>
    </main>
  );
};

export default Perkebunan;
